import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import {withFirebase} from '../Firebase'
import { AuthUserContext } from '../Session';
import {withAuthorizationNoRedirect} from '../Session';

class Voter extends React.Component {
  constructor(props) {
    super(props);

    this.state={
      map: [],
      user: null
    }
  }

  componentDidMount() {
    this.setState({ loading: true });

    this.props.firebase.maps().orderByChild('title').equalTo(this.props.mapName).on('value', snapshot => {
      const mapObject = snapshot.val();

      const mapList = Object.keys(mapObject).map(key => ({
        ...mapObject[key],
        uid: key,
      }));

      this.setState({
        map: mapList,
        loading: false,
      });
    });
  }

  componentWillUnmount() {
    this.props.firebase.maps().orderByChild('title').equalTo(this.props.mapName).off();
  }

  upDownVoteClickHandler = (e) => {
    e.preventDefault();
    const {map} = this.state
    let key = map[0].uid
    if(e.target.id === 'up') {
      this.props.firebase.db.ref('maps/' + key).update({votes: map[0].votes + 1})
    } else if(e.target.id === 'down') {
      this.props.firebase.db.ref('maps/' + key).update({votes: map[0].votes - 1})
    }
  }

  render() {

    const {map} = this.state

    return (
      <div className='w-100'>
      {map.map(map => (
        <>
        <a className="btn p-0" onClick={this.upDownVoteClickHandler} id='down'>
          <FontAwesomeIcon icon="caret-square-down" className='d-inline-block mx-3 vote'/>
        </a>
        <p className='d-inline-block voteT'>{map.votes}</p>
        <a className="btn p-0" onClick={this.upDownVoteClickHandler}  id='up'>
          <div id='up'>
            <FontAwesomeIcon icon="caret-square-up" className='d-inline-block mx-3 vote' id='up'/>
          </div>
        </a>
        </>
        ))}
      </div>
    )
  }
}

const authCondition = authUser => !!authUser;

export default withAuthorizationNoRedirect(authCondition)(withFirebase(Voter))
