import React from 'react';

import { AuthUserContext } from '../Session';
import SubmitForm from './SubmitForm'
import withAuthorization from '../Session/SubmitAuth';

const Submit = () => (
  <AuthUserContext.Consumer>
    {(authUser) => (
      <div>
        <SubmitForm owner={authUser.uid}/>
      </div>
    )}
  </AuthUserContext.Consumer>
);

const authCondition = authUser => !!authUser;

export default withAuthorization(authCondition)(Submit);
