import app from 'firebase/app';
import 'firebase/auth';
import 'firebase/database';

const config = {
  apiKey: "AIzaSyAnWtiYs11GQqRRNYi0Z0FGoUo57lKjT4Y",
  authDomain: "mccreations-c3cb8.firebaseapp.com",
  databaseURL: "https://mccreations-c3cb8.firebaseio.com",
  projectId: "mccreations-c3cb8",
  storageBucket: "mccreations-c3cb8.appspot.com",
  messagingSenderId: "92325017566",
  appId: "1:92325017566:web:949acc99082d5c969c4f4f",
  measurementId: "G-0MSTKN2G62"
};

class Firebase {
  constructor() {
    app.initializeApp(config);

    this.auth = app.auth();
    this.db = app.database();
  }

  // *** Auth API ***

  doCreateUserWithEmailAndPassword = (email, password) =>
    this.auth.createUserWithEmailAndPassword(email, password);

  doSignInWithEmailAndPassword = (email, password) =>
    this.auth.signInWithEmailAndPassword(email, password);

  doSignOut = () => this.auth.signOut();

  doPasswordReset = email => this.auth.sendPasswordResetEmail(email);

  doPasswordUpdate = password =>
    this.auth.currentUser.updatePassword(password);

  // *** User API ***

  user = uid => this.db.ref(`users/${uid}`);

  users = () => this.db.ref('users');

  maps = () => this.db.ref('maps')

  datapacks = () => this.db.ref('datapacks')

  resourcepacks = () => this.db.ref('resourcepacks')

  getMapFromName = name => this.db.ref('map').orderByChild('title').equalTo(`${name}`)
}

export default Firebase;
