import React, { Component } from 'react';
import axios from 'axios';
import {Progress} from 'reactstrap';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { Editor } from '@tinymce/tinymce-react';

import { withFirebase } from '../Firebase';
import {AuthUserContext} from '../Session';

class Submit extends Component {
  constructor(props) {
    super(props);
      this.state = {
        contentType: "",
        title: "",
        creator: "",
        social: "",
        shortDescription: "",
        description: "",
        download: "",
        resourcepack: "",
        version: "",
        contentVersion: "",
        images: "",
        logo: "",
        selectedImages: null,
        selectedLogo: null,
        loaded:0,
        owner: this.props.owner
      }

  }
  checkMimeType=(event)=>{
    //getting file object
    let files = event.target.files
    //define message container
    let err = []
    // list allow mime type
   const types = ['image/png', 'image/jpeg', 'image/gif']
    // loop access array
    for(var x = 0; x<files.length; x++) {
     // compare file type find doesn't matach
         if (types.every(type => files[x].type !== type)) {
         // create error message and assign to container
         err[x] = files[x].type+' is not a supported format\n';
       }
     };
     for(var z = 0; z<err.length; z++) {// if message not same old that mean has error
         // discard selected file
        toast.error(err[z])
        event.target.value = null
    }
   return true;
  }
  maxSelectFile=(event)=>{
    let files = event.target.files
        if (files.length > 3) {
           const msg = 'Only 3 images can be uploaded at a time'
           event.target.value = null
           toast.warn(msg)
           return false;
      }
    return true;
 }
 checkFileSize=(event)=>{
  let files = event.target.files
  let size = 2000000
  let err = [];
  for(var x = 0; x<files.length; x++) {
  if (files[x].size > size) {
   err[x] = files[x].type+'is too large, please pick a smaller file\n';
 }
};
for(var z = 0; z<err.length; z++) {// if message not same old that mean has error
  // discard selected file
 toast.error(err[z])
 event.target.value = null
}
return true;
}
onIChangeHandler=event=>{
  var files = event.target.files
  if(this.maxSelectFile(event) && this.checkMimeType(event) &&    this.checkFileSize(event)){
  // if return true allow to setState
     this.setState({
     selectedImages: files,
     loaded:0
  })
}
}

onDescriptionChangeHandler = (content, editor) => {
  this.setState({
    description: content
  })
}

onSubmitChangeHandler=event=>{
  if (event.target.id === 'content-type') {
    this.setState({
      contentType: event.target.value
    })
  } else if (event.target.id === 'title') {
    this.setState({
      title: event.target.value
    })
  } else if (event.target.id === 'creator') {
    this.setState({
      creator: event.target.value
    })
  } else if (event.target.id === 'social') {
    this.setState({
      social: event.target.value
    })
  } else if (event.target.id === 'short-description') {
    this.setState({
      shortDescription: event.target.value
    })
  } else if (event.target.id === 'desc') {
    this.setState({
      description: event.target.value
    })
  }  else if (event.target.id === 'download') {
    this.setState({
      download: event.target.value
    })
  } else if (event.target.id === 'rp') {
    this.setState({
      resourcepack: event.target.value
    })
  } else if (event.target.id === 'version') {
    this.setState({
      version: event.target.value
    })
  } else if (event.target.id === 'content-version') {
    this.setState({
      contentVersion: event.target.value
    })
  }
}

onLChangeHandler=event=>{
  var file = event.target.files[0]
  if(this.maxSelectFile(event) && this.checkMimeType(event) &&    this.checkFileSize(event)){
  // if return true allow to setState
     this.setState({
     selectedLogo: file,
     loaded:0
  })
}
}

  onClickHandler = (e, authUser) => {
    e.preventDefault();
    const data = new FormData()
    data.append('file', this.state.selectedLogo)
    for(var x = 0; x<this.state.selectedImages.length; x++) {
      data.append('file', this.state.selectedImages[x])
    }
    axios.post("https://php.mccreations.net:8000/upload", data, {
      onUploadProgress: ProgressEvent => {
        this.setState({
          loaded: (ProgressEvent.loaded / ProgressEvent.total*100),
        })
      },
    })
      .then(res => { // then print response status
        toast.success('Images Uploaded Successfully')
        var { contentType, title, creator, social, shortDescription, description, download, resourcepack, version, contentVersion, images, logo, selectedImages, selectedLogo, owner } = this.state;

        for(var i = 0; i<this.state.selectedImages.length; i++) {
          images = images + "|maps/images/" + selectedImages[i].name
        }
        logo = "maps/images/" + selectedLogo.name

        if (contentType === 'map') {
          const items = this.props.firebase.db.ref('maps')

          items.push({
            contentType: contentType,
            title: title,
            creator: creator,
            social: social,
            shortDescription: shortDescription,
            description: description,
            download: download,
            resourcepack: resourcepack,
            version: version,
            contentVersion: contentVersion,
            images: images,
            logo: logo,
            approved: false,
            featured: false,
            downloads: 0,
            clicks: 0,
            votes:0,
            owner: owner,
            createdDate: Date.now(),
            updatedDate: Date.now()
          }).then(() => {
            toast.success('Map Submitted Successfully')
          })
        } else if (contentType === 'datapack') {
          const items = this.props.firebase.db.ref('datapacks')

          items.push({
            contentType: contentType,
            title: title,
            creator: creator,
            social: social,
            shortDescription: shortDescription,
            description: description,
            download: download,
            resourcepack: resourcepack,
            version: version,
            contentVersion: contentVersion,
            images: images,
            logo: logo,
            approved: false,
            featured: false,
            downloads: 0,
            clicks: 0,
            votes:0,
            owner: owner,
            createdDate: Date.now(),
            updatedDate: Date.now()
          }).then(() => {
            toast.success('Datapack Submitted Successfully')
          })
        } else {
          const items = this.props.firebase.db.ref('resourcepacks')

          items.push({
            contentType: contentType,
            title: title,
            creator: creator,
            social: social,
            shortDescription: shortDescription,
            description: description,
            download: download,
            resourcepack: resourcepack,
            version: version,
            contentVersion: contentVersion,
            images: images,
            logo: logo,
            approved: false,
            featured: false,
            downloads: 0,
            clicks: 0,
            votes:0,
            owner: owner,
            createdDate: Date.now(),
            updatedDate: Date.now()
          }).then(() => {
            toast.success('Resourcepack Submitted Successfully')
          })
        }
      })
      .catch(err => { // then print response status
        toast.error('Image Upload Failed')
        console.log(err)
      })

        };

  render() {
    return (
      <div className="container">
        <div className="input-group mb-3">
          <div className="input-group-prepend">
            <span className="input-group-text">Content Type</span>
          </div>
          <select name="content" required="" className="custom-select" id='content-type' onChange={this.onSubmitChangeHandler}>
            <option value="">Choose Type </option>
            <option value="map">Map</option>
            <option value="datapack">Datapack</option>
            <option value="resourcepack">Resourcepack</option>
          </select>
      </div>
      <div className="input-group mb-3">
          <div className="input-group-prepend">
            <span className="input-group-text">Title</span>
          </div>
          <input type="text" required="" className="form-control" id="title" name="title" onChange={this.onSubmitChangeHandler}/>
        </div>
        <div className="input-group mb-3">
          <input type="text" required="" className="form-control" id="creator" placeholder="Creator" onChange={this.onSubmitChangeHandler}/>
          <input type="text" className="form-control" id="social" placeholder="Social Link" onChange={this.onSubmitChangeHandler}/>
        </div>
        <div className="input-group mb-3">
          <div className="input-group-prepend">
            <span className="input-group-text">Upload Logo</span>
          </div>
          <input type="file" className="form-control" onChange={this.onLChangeHandler}/>
        </div>
        <div className="form-group">
          <textarea maxLength="20000" placeholder="Short description (Will display on stuff like the homepage)" id="short-description" className="form-control" rows="3" onChange={this.onSubmitChangeHandler}></textarea>
        </div>
        <div className="form-group">
          <Editor onEditorChange={this.onDescriptionChangeHandler} init={{height: 300}}/>
        </div>
        <div className="input-group mb-3">
          <div className="input-group-prepend">
            <span className="input-group-text">Upload Images</span>
          </div>
          <input type="file" className="form-control" multiple onChange={this.onIChangeHandler}/>
        </div>
        <div className="input-group mb-3">
          <div className="input-group-prepend">
            <span className="input-group-text">Download Link</span>
          </div>
          <input type="text" required="" className="form-control" id="download" name="download" onChange={this.onSubmitChangeHandler}/>
        </div>
        <div className="input-group mb-3">
          <div className="input-group-prepend">
            <span className="input-group-text">Resourcepack Link</span>
          </div>
          <input type="text" className="form-control" id="rp" name="rp" onChange={this.onSubmitChangeHandler}/>
        </div>
        <div className="input-group mb-3">
          <input type="text" required="" className="form-control" id="version" placeholder="Minecraft Version" onChange={this.onSubmitChangeHandler}/>
          <input type="text" required="" className="form-control" id="content-version" placeholder="Content Version(usually 1.0)" onChange={this.onSubmitChangeHandler}/>
        </div>
        <div className="form-group">
          <ToastContainer />
          <Progress max="100" color="success" value={this.state.loaded} >{Math.round(this.state.loaded,2) }%</Progress>
        </div>
          <button type="button" className="btn btn-success btn-block" onClick={this.onClickHandler}>Submit</button>
      </div>
    );
  }
}

const SubmitForm = withFirebase(Submit)

export default SubmitForm;
