import React, { Component } from 'react';
import {Link} from 'react-router-dom'

import { withFirebase } from '../Firebase';
import Voter from '../Voting'

class MapDetail extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: false,
      map: [],
    };
  }

  componentDidMount() {
    this.setState({ loading: true });

    this.props.firebase.maps().orderByChild('title').equalTo(this.props.mapName).on('value', snapshot => {
      const mapObject = snapshot.val();

      const mapList = Object.keys(mapObject).map(key => ({
        ...mapObject[key],
        uid: key,
      }));

      this.setState({
        map: mapList,
        loading: false,
      });
    });
  }

  componentWillUnmount() {
    this.props.firebase.maps().orderByChild('title').equalTo(this.props.mapName).off();
  }


  render() {
    const { map, loading } = this.state;

    return (
      <div className='container mx-auto text-center border border-primary rounded pb-3'>
        {loading && <div>Loading ...</div>}

        {map.map(map => (
          <>
          <h1 className='mt-4 font-weight-bold'>{map.title}</h1>
          <img className='w-100 my-2' src={"../" + map.logo} />
          <div className='w-100 border border-primary rounded'>
            <p className='d-inline-block w-25'>ProfileImage</p>
            <div className='d-inline-block w-75 text-left'>
              <h4 className='mt-2'>{map.creator}</h4>
              <a href={map.social}>Website</a>
            </div>
          </div>
          <div className='d-inline-block w-75 py-3 pr-4'>
            <div className="border border-primary rounded p-2">{map.description}</div>
          </div>
          <div className='d-inline-block w-25 py-3 float-right'>
            <div className='border border-primary rounded p-2'>
              <a className='btn btn-primary w-100 mb-2 detDL' href={map.download}>Download</a>
              <Voter mapName={map.title}/>
              <p>Minecraft Version: {map.version}</p>
              <p>Downloads: {map.downloads}</p>
            </div>
          </div>
          <div className="carousel slide" id='mapSlide' data-ride="carousel">
            <div className="carousel-inner">
              <MapImages images={map.images}/>
            </div>
            <a className="carousel-control-prev" href="#mapSlide" role="button" data-slide="prev">
              <span className="carousel-control-prev-icon" aria-hidden="true"></span>
              <span className="sr-only">Previous</span>
            </a>
            <a className="carousel-control-next" href="#mapSlide" role="button" data-slide="next">
              <span className="carousel-control-next-icon" aria-hidden="true"></span>
              <span className="sr-only">Next</span>
            </a>
          </div>
          </>
        ))}
      </div>
    );
  }
}

const MapImages = ({images}) => {

  var imageList = images.split("|")

  imageList.shift()

  return (
    <>
    {imageList.map((image, index) => {

      {
        if (index==0) {
          return (
            <div className="carousel-item active">
              <img src={"../" + image} className="d-block w-100" alt="..." />
            </div>
          )
        } else {
          return (
            <div className="carousel-item">
              <img src={"../" + image} className="d-block w-100" alt="..." />
            </div>
          )
        }
      }
    }
  )
}
    </>
)}

export default withFirebase(MapDetail);
