import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';

import Navigation from '../Navigation';
import SignUpPage from '../SignUp';
import SignInPage from '../SignIn';
import PasswordForgetPage from '../PasswordForget';
import HomePage from '../Home';
import AccountPage from '../Account';
import AdminPage from '../Admin';
import MapsPage from '../Browse/Maps';
import DatapacksPage from '../Browse/Datapacks';
import ResourcepacksPage from '../Browse/Resourcepacks';
import SubmitPage from '../Submit'
import SubmitNonAuth from '../Submit/SubmitNonAuth'
import MapDetailPage from '../Content/MapWrap';

import * as ROUTES from '../../constants/routes';
import { withAuthentication } from '../Session';

const App = () => (
  <Router>
    <div>
      <Navigation />
      <Route exact path={ROUTES.SIGN_UP} component={SignUpPage} />
      <Route exact path={ROUTES.SIGN_IN} component={SignInPage} />
      <Route exact path={ROUTES.PASSWORD_FORGET} component={PasswordForgetPage} />
      <Route exact path={ROUTES.HOME} component={HomePage} />
      <Route exact path={ROUTES.ACCOUNT} component={AccountPage} />
      <Route exact path={ROUTES.ADMIN} component={AdminPage} />
      <Route exact path={ROUTES.MAPS} component={MapsPage} />
      <Route exact path={ROUTES.DATAPACKS} component={DatapacksPage} />
      <Route exact path={ROUTES.RESOURCEPACKS} component={ResourcepacksPage} />
      <Route exact path={ROUTES.SUBMIT} component={SubmitPage} />
      <Route path="/map/:mapName" component={MapDetailPage} />
      <Route exact path={ROUTES.SUBMITNONAUTH} component={SubmitNonAuth} />

    </div>
  </Router>
);

export default withAuthentication(App);
