import React, { Component } from 'react';
import {Link} from 'react-router-dom'

import { withFirebase } from '../Firebase';
import Card from '../Card'

class Datapacks extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: false,
      packs: [],
    };
  }

  componentDidMount() {
    this.setState({ loading: true });

    this.props.firebase.datapacks().on('value', snapshot => {
      const packObject = snapshot.val();

      if (packObject != null) {

        const packsList = Object.keys(packObject).map(key => ({
          ...packObject[key],
          uid: key,
        }));

        var approvedPacksList = []

        for (var pack in packsList) {
          if (packsList.hasOwnProperty(pack)) {
            if(packsList[0].approved) {
              approvedPacksList.push(packsList[0])
            }
          }
        }

        this.setState({
          packs: approvedPacksList,
          loading: false,
        });

      }
    });
  }

  componentWillUnmount() {
    this.props.firebase.datapacks().off();
  }

  render() {
    const { packs, loading } = this.state;

    return (
      <div className='mx-5'>
        <h1>Datapacks</h1>

        {loading && <div>If you see this message for more than a couple of seconds something has gone wrong! Let us know so that we can fix it.</div>}

        <MapList packs={packs} />
      </div>
    );
  }
}

const MapList = ({ packs }) => (
  <div className='card-deck'>
    {packs.map(pack => (
      <Card map={pack} />
    ))}
  </div>
);

export default withFirebase(Datapacks);
