import React, { Component } from 'react';
import {Link} from 'react-router-dom'

import { withFirebase } from '../Firebase';
import Card from '../Card'

class Maps extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: false,
      maps: [],
    };
  }

  componentDidMount() {
    this.setState({ loading: true });

    this.props.firebase.maps().on('value', snapshot => {
      const mapsObject = snapshot.val();

      if (mapsObject != null) {

        const mapsList = Object.keys(mapsObject).map(key => ({
          ...mapsObject[key],
          uid: key,
        }));

        var approvedMapsList = []

        for (var map in mapsList) {
          if (mapsList.hasOwnProperty(map)) {
            if(mapsList[map].approved) {
              approvedMapsList.push(mapsList[map])
            }
          }
        }

        approvedMapsList.sort(function(a, b){return b.updatedDate - a.updatedDate})

        this.setState({
          maps: approvedMapsList,
          loading: false,
        });
      }
    });
  }

  componentWillUnmount() {
    this.props.firebase.maps().off();
  }

  render() {
    const { maps, loading } = this.state;

    return (
      <div className='mx-5'>
        <h1>Maps</h1>

        {loading && <div>If you see this message for more than a couple of seconds something has gone wrong! Let us know so that we can fix it.</div>}

        <MapList maps={maps} />
      </div>
    );
  }
}

const MapList = ({ maps }) => (
  <div className='card-deck'>
    {maps.map(map => (
      <Card map={map} />
    ))}
  </div>
);

export default withFirebase(Maps);
