import React from 'react'
import {formatDistance} from 'date-fns'

const HoverOverlay = (props) => {

  return(
    <div className='mapHoverOverlay'>
      <div className='d-inline-block mr-5 w-50'>
        <h5 className='font-weight-bold'>Version</h5>
        <p>{props.map.version}</p>
      </div>
      <div className='d-inline-block'>
        <h5 className='font-weight-bold'>Votes</h5>
        <p>{props.map.votes}</p>
      </div>
      <h5 className='font-weight-bold'>Created By</h5>
      <p>{props.map.creator}</p>
      <p className="updatedText"><small className='text-muted'>Last updated {formatDistance(new Date(props.map.updatedDate), new Date())} ago</small></p>
    </div>
  )
}

export default HoverOverlay
